import * as yup from "yup";

const cartRegExp = /(\d{4})( )(\d{4})( )(\d{4})( )(\d{4})/;
const yearRegExp = /(\d{2})(\/)(\d{2})/;
const cvvRegExp = /(\d{3})/;

export const validationsSchemaPayment = yup.object().shape({
  card: yup
    .string()
    .required("   ")
    .matches(cartRegExp, " "),
  cardDate: yup
    .string()
    .matches(yearRegExp, " ")
    .required("   "),
  cardCVV: yup
    .string()
    .matches(cvvRegExp, " ")
    .required("   "),
});
