import * as yup from "yup";

const phoneRegExp = /( )(\()(29|25|(44)|33)(\))(\d{3})(\d{2})(\d{2})$/;

export const validationsSchemaStore = yup.object().shape({
  phone: yup
    .string()
    .required("   ")
    .matches(phoneRegExp, " "),
  email: yup
    .string()
    .email(" email")
    .required("   "),
  country: yup.string().required("   "),
  storeAddress: yup.string().required("   "),
});
